body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: url('./background.jpg');
}

#root {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transition: background 1s;
  -o-transition: background 1s;
  transition: background 1s;
}


